.week_about h1 {
    font-family: "Montserrat", sans-serif;
    color: white;
    font-size: 50px;

    text-align: center;
}

.week_about p {
    font-family: "Montserrat", sans-serif;
    color: white;
    font-size: 32px;

    text-align: center;
    word-wrap: break-word;
    
    margin-left: 25%;
    margin-right: 25%;
}

.right_button_holder button{
    margin-top: 10px;
    border-radius: 15px;
    border: 5px solid rgba(255, 255, 255, 1);
    background: none;
  
    font-family: "Montserrat", sans-serif;
    color: white;
    font-size: 32px;

    cursor: pointer;
    transition: 0.3s linear;
  }

.right_button_holder{
    display: flex;
    justify-content: center;
}

.right_button_holder button:hover{
    background-color: #1c1b1b;
    transition: 0.3s linear;
}